<template>
  <div class="root" v-if="schema.root && schema.root.layout.type == 'VerticalList'" :style="{width:schema.root.width,height:schema.root.height}">
    <!-- Another types of windows-->
    <div class="child" v-if="!schema.root['data-source'] && !schema.root.children.submit"  v-for="children, index in schema.root.children" v-bind:key="index">
       <component  :is="'dynamic'+children.type"  :schema="children"/>
    </div>

     <!-- Display update or insert  operations-->
    <dynamicform v-if="schema.root['data-source'] || schema.root.children.submit" :schema="schema" :selected="id"/>

  </div>
</template>

<script>
import Components from '../components'

export default {
  name: 'dynamicBody',
  computed: {
    returnSchema: function () {
      if (this.schema != null) {
        return this.schema
      }

      return {}
    },
    returnId: function () {
      if (this.id != null) {
        return this.id
      }
    }
  },
  data () {
    return {}
  },
  props: {
    schema: {
      type: Object,
      default: () => {
        return {}
      }
    },
    id: {
      type: String,
      default: '0'
    }
  },
  components: Components,
  methods: {},
  created () {}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.root {
  text-align: initial;
}
</style>



