<template>
<div class="chat">
  <span class="pageTitle">Chat</span><button class="createRoomBtn" @click="showRoomInput=!showRoomInput">Create room</button>
      <button type="button" @click="notify">Show notification</button>
      <el-button type="text" @click="dialogVisible = true">click to open the Dialog</el-button>
  <transition name="fade">
  <input class="roomNameInput" v-model="roomNameInput" v-if="showRoomInput" @keyup.enter="createRoom()"></input>
</transition>
<button class="createRoomBtn" @click="sendEventUserID()">Send Event</button>
<input value="3" placeholder="user id" v-model="eventuser"></input>
  <hr>
  <!-- columns should be the immediate child of a .row -->
  <div class="row">

    <div class="three columns col-left">
    <div class="scroll-rooms" ref="roomcontainer">
    <div v-for='(room, index) in rooms' :key="index">
      <div>

      <button @click="joinRoom(room.roomID),updateUnreadCounter('room',room.roomID)" >{{room.title}}
      <span class="unread-badge">{{room.data.unread}}</span>
      </button>
      <button class="btn-leave" @click="leaveRoom(room.roomID,index)"></button>
      <button class="btn-del" @click="deleteRoom(room.roomID,index)">x</button>

    </div>
    </div>
    <button @click="loadRooms(currentRoomPage,1)">Load More</button>


  </div>


</div>

    <div class="six columns col-left">
      <div class="messages-container" ref="msgcontainer">


        <div v-for='(message, index) in messages' :key="index">

        <div v-if="message.message.is_notification === 1">
          <div class="system-message-body">
            <span class="system-message">System: </span><span>{{message.message.message}}</span>
          </div>
        </div>
        <div v-else>
        <div class="message-box">
          <div v-if="message.user!=null"><span class="message-username">{{message.user.firstname}}</span><sup>
          <span class="message-time">{{message.created}}</span></sup>
          </div>
          <div v-else>
          <span class="message-username" v-if="message.guest.name!=null">{{message.guest.name}}</span>
          <span class="message-username" v-else>Guest #{{message.guest.id}}</span>
          <sup><span class="message-time">{{message.created}}</span></sup>
          </div>
          <div class="message-body">{{message.message.message}}</div>
        </div>
      </div>

        </div>
        <!-- <br><br>

        <div class="message-box box-right">
          <span class="message-username">John Doe</span><sup><span class="message-time">10m</span></sup>
          <div class="message-body">Hi !</div>
        </div> -->



      </div>
      <textarea class="u-full-width textarea-input" placeholder="Your message here " v-model="messageInput"></textarea>
      <br>
      <button @click="sendMessage()">Send</button>

    </div>

    <div class="three columns">
      <div class="scroll-employees" ref="employeecontainer">
      <div v-for='(employee, index) in employees' :key="index">
  <button @click="joinRoom(employee.data.room_id,employee.data.user_id)" v-if="employee.data.room_id==='0'">
  {{employee.data.firstname}} {{employee.data.lastname}}
  </button>
  <button @click="joinRoom(employee.data.room_id),updateUnreadCounter('employee',employee.roomID,employee.userID)" v-else>{{employee.data.firstname}} {{employee.data.lastname}}
      <span class="unread-badge">{{employee.data.unread}}</span>
  </button>


      </div>
      <button @click="loadEmployees(currentEmployeePage,1)">Load More</button>
    </div>
    </div>

  </div>

  <notifications group="chat" />
  <el-dialog
    title="Tips"
    :visible.sync="dialogVisible"
    width="30%"
    :before-close="handleClose">
    <span>This is a message</span>
    <span slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false">Cancel</el-button>
      <el-button type="primary" @click="dialogVisible = false">Confirm</el-button>
    </span>
  </el-dialog>
</div>
</template>

<script>
import 'skeleton-css/css/normalize.css'
import { mapGetters } from 'vuex'
import chatMixin from '../../../mixins/modules/chat/chat.js'
import eventMixin from '../../../mixins/modules/chat/event.js'
import notificationMixin from '../../../mixins/modules/chat/notification.js'
import handlerMixin from '../../../mixins/modules/chat/handler.js'
import io from 'socket.io-client'
import VueSocketio from 'vue-socket.io'
import Vue from 'vue'
import VueNativeNotification from 'vue-native-notification'
import Visibility from 'visibilityjs'
import Notifications from 'vue-notification'

import Element from 'element-ui'

// socket connection
var socket = io('https://dev.evrica.io:8085', {
  reconnection: false
})

Vue.use(VueSocketio, socket)
Vue.use(Element)
Vue.use(Notifications)
Vue.use(VueNativeNotification, {
  // Automatic permission request before
  // showing notification (default: true)
  requestOnNotify: true
})
Vue.use(Visibility)

export default {
  computed: mapGetters(['getToken']),
  name: 'Chat',
  data () {
    return {
      token: '',
      rooms: [],
      employees: [],
      currentRoomPage: 1,
      currentEmployeePage: 1,
      currentMessagePage: 1,
      currentRoomID: 0,
      messages: [],
      messageInput: 'Hello ?',
      roomNameInput: '#hobbies',
      showRoomInput: false,
      eventuser: 3,
      visiblePage: true,
      updateListEmployees: false,
      showModal: false,
      roomArrayIndex: null,
      dialogVisible: false
    }
  },
  mixins: [chatMixin, eventMixin, notificationMixin, handlerMixin],
  components: {
  },
  watch: {
  },
  sockets: {
    connect: function () {
      console.log('socket connected')
    },
    message: function (msg) {
      console.log(msg)

      if (typeof msg.data.notification !== 'undefined') {
        this.notifyUser(msg.data.notification)
      } else if (typeof msg.data.customEvent !== 'undefined') {
        this.processEvents(msg.data.customEvent)
      } else {
      // if message came from auth
        if (msg.event === 'auth') {
        // your code here
          this.loadRooms(this.currentRoomPage, 1)
          this.loadEmployees(this.currentEmployeePage, 1)
        } else if (msg.event === 'getRooms') {
          this.displayRooms(msg.data.message.rooms)
        } else if (msg.event === 'getEmployees') {
          this.displayEmployees(msg.data.message.employees)
        } else if (msg.event === 'joinChatRoom') {
          if (msg.data.message.showMessages === true && parseInt(this.currentRoomID) === parseInt(msg.data.message.roomID)) {
            if (socket.id === msg.data.message.connection) {
              this.showRoomMessages(msg.data.message.roomID, this.currentMessagePage, 15)
            }
          }
        } else if (msg.event === 'showRoomMessages') {
        // console.log(msg.data.message.message)
          this.displayMessages(msg.data.message.message)
        } else if (msg.event === 'sendMessageRoom') {
          if (parseInt(msg.data.message.roomID) === parseInt(this.currentRoomID)) {
            this.displayMessages(msg.data.message.message)
          }
        } else {
        // console.log(msg)
        }
      }
    }
  },
  mounted: function () {
  },
  created: function () {
    // set token for current user
    this.token = this.getToken

    // set auth data
    var data = { 'event': 'auth', 'data': { 'token': this.token } }

    this.$socket.json.send(data)

    var self = this

    Visibility.change(function (e, state) {
      document.title = state
      self.pageState(state)
    })
  },
  methods: {
    notify () {
      this.$notification.show('Notify', {
        body: 'This is an example!',
        icon: 'https://d30y9cdsu7xlg0.cloudfront.net/png/65535-200.png'
      }, {})
    }
  }
}
</script>






<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped="scss">
@import 'skeleton-css/css/skeleton.css';
@import url('https://fonts.googleapis.com/css?family=Montserrat|Raleway');

.chat {
    font-family: 'Montserrat', 'Raleway' , sans-serif;
    font-size: 0.9em;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: left;
    color: #2c3e50;
    margin-top: 0px;
    margin-left: 40px;
    margin-right: 40px;
}

.col-left{
  border-right:1px solid grey;
}

.scroll-rooms{
  overflow-y: scroll;
  overflow-x: hidden;
  max-height: 300px;
}

.scroll-employees{
  overflow-y: scroll;
  overflow-x: hidden;
  max-height: 300px;
}

button{
  text-transform: lowercase;
  padding-left: 10px;
  padding-right: 10px;
}

.message-username{

}

.message-time{
  margin-left: 0.5em;
  font-size: 1.1em;
}

.message-body{
  font-size: 1em;
  font-weight: bold;
  margin-top: 0.5em;
}

.message-box{
  display: inline-block;
  padding: 0.8em;
  background-color: #E5E5EA;
  color: #000;
  border-radius: 10px;
  margin-bottom: 2em;
}

.box-right{
  float: right;
  margin-right: 2em;
  background-color: #00a8ff;
  color: #fff;
}

.messages-container{
  height: 300px;
  max-height:300px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.textarea-input{
  width: 95%;
}

.warning{
  background-color: #e74c3c;
  font-weight: bold;
}

.createRoomBtn{
  margin-left: 1em;
}

.pageTitle{
  font-size: 2em;
  font-weight: bold;
}

.roomNameInput{
    display: inline-block;
    height: 30px;
    padding: 0 10px;
    color: #555;
    font-size: 11px;
    font-weight: bold;
    line-height: 38px;
    letter-spacing: .1rem;
    text-decoration: none;
    white-space: nowrap;
    background-color: transparent;
    border-radius: 4px;
    border: 1px solid #bbb;
    cursor: pointer;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

.unread-badge{
  background-color: #ee5253;
  color: #fff;
  font-weight: bold;
  padding-top: 0.5em;
  padding-bottom: 0.5em;
  padding-left: 0.7em;
  padding-right: 0.6em;
  border-radius: 50%;
}

.colored-box{
  width: 30px;
  height: 30px;
}

.btn-leave,.btn-del{
  color: #000;
  font-weight: bold;
  font-size: 1.5em;
}

.system-message{
  font-weight: bold;
}

.system-message-body{
  margin-bottom: 1em;
}

</style>



