export default {
  methods: {
    notifyUser: function (data) {
      if (parseInt(data.roomID) !== parseInt(this.currentRoomID) || this.visiblePage === false) {
        if (this.visiblePage === true) {
          this.$notify({
            group: 'chat',
            type: (data.status === false) ? 'error' : 'success',
            duration: 500,
            title: 'System !',
            text: data.message
          })
        } else {
          this.$notification.show('Notify', {
            body: data.message,
            icon: 'https://d30y9cdsu7xlg0.cloudfront.net/png/65535-200.png'
          }, {})
        }

        // identify if room is private (one - one user conversation) or public
        if (data.default === false) {
          console.log('is default')
          if (data.roomEvent === true) {
            var onElementEmployee = {roomID: data.roomID, userID: data.userID}
            this.updateUnreadMessagesCounter('employee', onElementEmployee, true)
          }
        } else {
          if (data.roomEvent === true) {
            var onElementRoom = {roomID: data.roomID, userID: data.userID}
            this.updateUnreadMessagesCounter('room', onElementRoom, true)
          }
        }
      }
    }
  }
}



