import moment from 'moment'
export default {
  methods: {
    moment: function (date) {
      return moment(date)
    },
    loadRooms: function (offset, amount) {
      // set offset and limit to rooms
      var data = { 'event': 'getRooms', 'data': { page: offset, limit: amount } }

      this.$socket.json.send(data)
    },
    loadEmployees: function (offset, amount) {
      // set offset and limit to rooms
      var data = { 'event': 'getEmployees', 'data': { page: offset, limit: amount } }

      this.$socket.json.send(data)
    },
    setupEmployeeRoom: function (id) {
      var data = { 'event': 'setupEmployeeRoom', 'data': { employeeID: id } }
      this.$socket.json.send(data)
    },
    joinRoom: function (id, referrerID = null) {
      console.log(id)
      var data
      if (referrerID !== null) {
        data = { 'event': 'joinChatRoom', 'data': { roomID: id, employeeID: referrerID } }
      } else {
        data = { 'event': 'joinChatRoom', 'data': { roomID: id } }
      }
      this.$socket.json.send(data)
      this.currentRoomID = id
      this.messages = []
    },
    createRoom: function () {
      var data = { 'event': 'createRoom', 'data': { title: this.roomNameInput } }
      this.$socket.json.send(data)
    },
    leaveRoom: function (id, index) {
      var data = { 'event': 'leaveRoom', 'data': { roomID: id } }
      this.$socket.json.send(data)
    },
    deleteRoom: function (id, index) {
      var data = { 'event': 'deleteRoom', 'data': { roomID: id } }
      this.$socket.json.send(data)
      this.roomArrayIndex = index
    },
    showRoomMessages: function (id, offset, amount) {
      var data = { 'event': 'showRoomMessages', 'data': { roomID: id, page: offset, limit: amount } }

      this.$socket.json.send(data)
    },
    displayRooms: function (rooms) {
      var i
      for (i = 0; i < rooms.length; i++) {
        this.rooms.push({roomID: rooms[i].room_id, title: rooms[i].title, data: rooms[i]})
      }

      if (rooms.length >= 1) {
        this.currentRoomPage++
      }

      console.log('Current rooms page ' + this.currentRoomPage)

      var self = this
      setTimeout(function () {
        var container = self.$refs.roomcontainer
        container.scrollTop = container.scrollHeight
      }, 50)
    },
    displayEmployees: function (employees) {
      var i
      for (i = 0; i < employees.length; i++) {
        this.employees.push({roomID: employees[i].room_id, userID: employees[i].user_id, data: employees[i]})
      }

      if (employees.length >= 1) {
        this.currentEmployeePage++
      }

      console.log('Current employee page ' + this.currentEmployeePage)

      var self = this
      setTimeout(function () {
        var container = self.$refs.employeecontainer
        container.scrollTop = container.scrollHeight
      }, 50)
    },
    displayMessages: function (messages) {
      messages.reverse()
      var i
      for (i = 0; i < messages.length; i++) {
        this.messages.push({guest: messages[i].guest, user: messages[i].user, message: messages[i], created: this.moment(messages[i].created_at).fromNow()})
      }
      var self = this
      setTimeout(function () {
        var container = self.$refs.msgcontainer
        container.scrollTop = container.scrollHeight
      }, 50)
    },
    sendMessage: function () {
      if (this.currentRoomID !== 0) {
        var data = { 'event': 'sendMessageRoom', 'data': { roomID: this.currentRoomID, message: this.messageInput } }

        this.$socket.json.send(data)
      } else {
        this.$notify({
          group: 'chat',
          type: 'error',
          duration: 5000,
          title: 'System',
          text: "Can't send a message , because you are not in room"
        })
      }
    },
    pageState: function (state) {
      if (state === 'visible') {
        this.visiblePage = true
      } else {
        this.visiblePage = false
      }
    },
    updateUnreadMessagesCounter: function (type, element, action) {
      switch (type) {
        case 'employee':
          this.employees.map((e, i) => {
            if (parseInt(e.roomID) === parseInt(element.roomID) && parseInt(e.userID) === parseInt(element.userID)) {
            // type is true increment with +1
              if (action === true) {
                this.employees[i].data.unread += 1
              } else {
              // else set to 0
                this.employees[i].data.unread = 0
              }
            }
          })
          break
        case 'room':
          this.rooms.map((e, i) => {
            if (parseInt(e.roomID) === parseInt(element.roomID)) {
          // type is true increment with +1
              if (action === true) {
                this.rooms[i].data.unread += 1
              } else {
            // else set to 0
                this.rooms[i].data.unread = 0
              }
            }
          })
          break
        default:
      }
    },
    updateUnreadCounter: function (type, roomID, userID = null) {
      // identify where counter needs to be updated
      switch (type) {
        case 'employee':
          var onElementEmployee = {roomID: roomID, userID: userID}
          this.updateUnreadMessagesCounter(type, onElementEmployee, false)
          break

        case 'room':
          var onElementRoom = {roomID: roomID}
          this.updateUnreadMessagesCounter(type, onElementRoom, false)
          break

        default:
          console.log('undefined update unread')
      }
    }

  }
}



