import Vue from 'vue'
import Router from 'vue-router'
import Auth from '@/components/pages/Auth'
import Main from '@/components/pages/Main'
import Settings from '@/components/pages/Settings'
import DynamicUI from '@/components/pages/DynamicUI'
import Chat from '@/components/pages/Chat'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Main',
      component: Main
    },
    {
      path: '/auth',
      name: 'Auth',
      component: Auth
    },
    {
      path: '/chat',
      name: 'Chat',
      component: Chat
    },
    {
      path: '/settings',
      name: 'Settings',
      component: Settings
    },
    {
      path: '/page/:entity',
      component: DynamicUI,
      props: true
    },
    {
      path: '/page/:entity/:id',
      component: DynamicUI,
      props: true
    }
  ]
})



