<template>
  <span>
  <component v-if="schema.class || schema.type" :is="schema.class?schema.class:schema.type">{{schema.value}}</component>
  <span v-if="value && options.key">{{parseVars(value,options.key)}}</span>
  </span>
</template>

<script>
import { mapGetters } from 'vuex'
import helper from '../components/utils/HelperDynamicUI'

export default {
  name: 'dynamiclabel',
  computed: mapGetters(['getSchema', 'getMetaDataErrors']),
  data () {
    return {}
  },
  mixins: [
    helper
  ],
  props: {
    schema: {
      type: Object,
      default: () => {
        return {}
      }
    },
    options: {
      type: Object,
      default: () => {
        return {}
      }
    },
    value: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  components: {},
  methods: {
    parseVars (obj, prop) {
      let value = this.fetchFromObject(obj, prop)
      if (!value) {
        let mainPart = prop.substring(0, prop.lastIndexOf('.'))
        let secondPart = prop.substring(prop.lastIndexOf('.') + 1, prop.length)

        value = this.fetchFromObject(obj, mainPart + '.i18n.' + secondPart)

        if (!value) {
        //  console.log('Label value not found, key: ' + prop)
          return ''
        }

        return value
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



